        <div class="col-3" v-for="image in images" :key="image.id">
            <router-link :to="{path:'livres/'+ image.id}"><img :src="image.url" :alt="image.alt"  /></router-link>
        </div>
    </div>
</div>
</template>
<script>

export default {
  data () {
    return {
      images: [
        { id: '1', url: 'https://images-na.ssl-images-amazon.com/images/I/41ccK56avDL._SX307_BO1,204,203,200_.jpg', alt: 'I love you nature' },
        { id: '2', url: 'https://images-na.ssl-images-amazon.com/images/I/41nRhYURvwL._SX342_BO1,204,203,200_.jpg', alt: 'Now with dog - Rosé' },
        { id: '3', url: 'https://images-na.ssl-images-amazon.com/images/I/51V9ptR45CL._SX318_BO1,204,203,200_.jpg', alt: 'Jeg er i Danmark' },
        { id: '4', url: 'https://images-na.ssl-images-amazon.com/images/I/51tKBsroH-L._SX331_BO1,204,203,200_.jpg', alt: 'Badabimbadabum' },
        { id: '5', url: 'https://images-na.ssl-images-amazon.com/images/I/51w1lIDfE9L._SX403_BO1,204,203,200_.jpg', alt: 'Goodmorning el mundo' },
        { id: '6', url: 'https://images-na.ssl-images-amazon.com/images/I/61+hg7MZDNL.jpg', alt: 'I love you nature' }
      ]
    }
  }
}
</script>

<style>
    img {
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 5px;
  margin: 5px;
  max-width: 75%;
  height: auto;
}
</style>


